.grid{
    margin:200px 0 0;
    
}

body{
    background-color:rgb(245, 192, 192);
}

#dijkstrasButton{
    color:white;
    background-color: red;
    border:none;
    height:40px;
    border-radius:15px;
    margin-top:50px;
    font-size: 25px;
    font-style:italic;
    box-shadow: 0 12px 16px 0 rgba(172, 63, 63, 0.445), 0 17px 50px 0 rgba(211, 73, 73, 0.19);
}

#dijkstrasButton:hover{
    color:white;
    background-color: green;
    border:none;
    height:40px;
    border-radius:5px;
    margin-top:50px;
    box-shadow: 0 8px 16px 0 rgba(26, 170, 110, 0.699), 0 6px 20px 0 rgba(50, 255, 31, 0.425);
    transform: translateY(4px);
    
}

#dijkstrasButton:active{
    transform: translateY(4px); 
    box-shadow: 0 5px #666; 
    background-color: #3e8e41;
}

