.node {
    width: 25px;
    height: 25px;
    outline: 1px solid black;
    display: inline-block;
  }
  
  .node-finish {
    background-color: blue;
  }
  
  .node-start {
    background-color: yellow;
  }
  
  .node-visited {
    animation-name: visitedAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction:alternate-reverse;
    animation-iteration-count: 1;
    animation-fill-mode:both;
    animation-play-state: running;
  }
  
  @keyframes visitedAnimation {
    0% {
      transform: scale(0.8);
      background-color: rgba(129, 0, 108, 0.75);
      border-radius: 100%;
    }
  
    50% {
      background-color: rgba(238, 23, 220, 0.75);
    }
  
    75% {
      transform: scale(1.2);
      background-color: rgba(202, 94, 235, 0.75);
    }
  
    100% {
      transform: scale(0.5);
      background-color: rgba(107, 56, 100, 0.75);
    }
  }
  
  .node-wall {
    background-color: red;
  }
  
  .node-shortest-path {
    animation-name: shortestPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes shortestPath {
    0% {
      transform: scale(0.6);
      background-color: rgb(23, 238, 23);
    }
  
    50% {
      transform: scale(1.2);
      background-color: rgb(24, 153, 24);
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(31, 235, 224);
    }
  }